
.varieties-outer-container {
    min-height: 75vh;
    background-color: #FFFFFF;
    background-image: url("https://images.unsplash.com/photo-1515615575935-36f6e462f56d?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2980&q=80");
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
}

.varieties-inner-container {
    top: 0; right: 0; bottom: 0; left: 0; background-color: rgba(0, 0,0,.8);
    min-height: 75vh;
    overflow: hidden;
}

.varieties-heading-container {
    width: 50%;
    padding: 1.5rem;
    background-color: white;
    border-radius: 5px;
    color: var(--brown);
    margin: 3rem auto 2rem auto;
}

.varieties-grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    justify-content: flex-start;
    align-items: flex-start;
    justify-items: center;
    align-content: flex-start;
}


.varieties-form {
    background-color: transparent;
    color: var(--brown);
    font-weight: 700;
    margin: auto;
    padding: 2rem 5rem;
    text-align: left;
    border: none;
}

.varieties-continue-btn {
    margin: 5rem auto 3rem auto;
}

.varieties-save-alert{
    position: absolute;
    width: 300px;
    left: 40%;
    top: 115px;
}
.varieties-delete-alert{
    position: absolute;
    width: 300px;
    left: 40%;
    top: 115px;
}

.varieties-remove-btn {
    font-size: 0.65rem;
    position: relative;
    bottom:5px;
}